var keyboardMapper = function (options) {
    var status = options.status;
    if (status.states.indexOf('disable') > -1)
        return status;
    if (options.event.keyCode !== options.keycode)
        return status;
    switch (options.event.type) {
        case 'keydown':
            status.setState('active');
            break;
        case 'keyup':
            status.unsetState('active');
            break;
    }
    return status;
};
export default keyboardMapper;
